input {
  width: calc(100% - 20px);
  height: 100%;
  background-color: transparent;
  border: 0;
  outline: 0;
  font-size: 15px;
  padding-left: 10px;
  padding-right: 10px;
}

input::placeholder {
  color: #000000;
}

.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side,#2e86de 94%,#0000) top/8px 8px no-repeat,
    conic-gradient(#0000 30%,#2e86de);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 8px),#000 0);
  animation: l13 1s infinite linear;
}
@keyframes l13{
  100%{transform: rotate(1turn)}
}
